.button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 16px 24px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.button:hover, .button:hover {
  background-color: #3e8e41;
}

.button_start:disabled {
  opacity: .5;
}

.container {
  width: 200px;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

